import {
  Tooltip,
  Checkbox,
  Tabs,
  Button,
  Radio,
  Input,
  Space,
  Card,
} from "antd";

import TabTable from "./TabTable";
import React, { useState } from "react";
import { major, refinement, common } from "./data";
import { Select } from "antd";

const { Option } = Select;

const { TabPane } = Tabs;

// 두개 이상 체크가 되어야함
const Common = ({ c, setB, setC, comCommon, setComCommon }) => {
  return (
    <>
      <span
        style={{
          width: "120px",
          fontSize: "20px",
          fontWeight: "bold",
          borderRadius: "20px 20px 20px 20px",
        }}
      >
        공통 졸업요건 진단
      </span>
      <br />
      <Card
        style={{
          width: "1250px",
          border: "1px solid gray",
          marginTop: "10px",
          borderRadius: "20px 20px 20px 20px",
        }}
      >
        <div
          style={{
            pointerEvents: c && "none",
            opacity: c && "0.4",
          }}
        >
          <Tooltip
            title={
              <ul style={{ marginLeft: "-15px", marginTop: "10px" }}>
                <li> 1. 주 전공을 80학점 이상 이수한 자</li>
                <li>2. 공학인증 또는 건축학인증을 수행한 자</li>
              </ul>
            }
          >
            <Checkbox
              onChange={(e) => {
                setComCommon({ ...comCommon, a: e.target.checked });
              }}
              style={{ fontWeight: "bold" }}
            >
              심화 전공 인증
            </Checkbox>
          </Tooltip>
          <Tooltip
            title={
              <ul style={{ marginLeft: "-15px", marginTop: "10px" }}>
                <li>
                  1. 외부기관에서 실시하는 공인어학시험 기준 통과자 (재학 중
                  취득한 성적에 한 함)
                </li>
                <li>2. 대학영어 1,2,3,4,5(총15학점) 이수자</li>
              </ul>
            }
          >
            <Checkbox
              onChange={(e) => {
                setComCommon({ ...comCommon, b: e.target.checked });
              }}
              style={{ marginLeft: "300px", fontWeight: "bold" }}
            >
              외국어 인증
            </Checkbox>
          </Tooltip>
          <Tooltip
            title={
              <ul style={{ marginLeft: "-15px", marginTop: "10px" }}>
                <li>
                  복수전공, 부전공, 교직, 학생군사교육단 과정 중 1개 이상 이수한
                  자
                </li>
              </ul>
            }
          >
            <br />
            <Checkbox
              onChange={(e) => {
                setComCommon({ ...comCommon, c: e.target.checked });
              }}
              style={{ fontWeight: "bold" }}
            >
              다전공 인증
            </Checkbox>
          </Tooltip>
          <Tooltip
            title={
              <ul style={{ marginLeft: "-15px", marginTop: "10px" }}>
                <li>
                  교양교육과정에 있는 독서토론1,2,3,4,5,6(총18학점) 이수자
                </li>
              </ul>
            }
          >
            <Checkbox
              onChange={(e) => {
                setComCommon({ ...comCommon, d: e.target.checked });
              }}
              style={{ marginLeft: "318px", fontWeight: "bold" }}
            >
              독서토론 인증
            </Checkbox>
          </Tooltip>
          <Tooltip
            title={
              <ul style={{ marginLeft: "-15px", marginTop: "10px" }}>
                <li>
                  우리대학 현장실습 규정에 의거 6학점이상 이수한 자
                  (국제교류팀에서 실시하는 해외현장실습은 KGU + 인증과 중복 인증
                  불허)
                </li>
              </ul>
            }
          >
            <br />
            <Checkbox
              onChange={(e) => {
                setComCommon({ ...comCommon, e: e.target.checked });
              }}
              style={{ fontWeight: "bold" }}
            >
              현장실습 인증
            </Checkbox>
          </Tooltip>
          <Tooltip
            title={
              <ul style={{ marginLeft: "-15px", marginTop: "10px" }}>
                <li>1. 비교과프로그램 140포인트 이상 취득</li>
                <li>
                  2. 비교과프로그램 중 커리어개발형 만으로 140포인트 이상 취득할
                  경우 직무역량인증으로 인정
                </li>
              </ul>
            }
          >
            <Checkbox
              onChange={(e) => {
                setComCommon({ ...comCommon, f: e.target.checked });
              }}
              style={{ marginLeft: "304px", fontWeight: "bold" }}
            >
              KGU포인트 140점 이상
            </Checkbox>
          </Tooltip>
        </div>
      </Card>
      <Button
        style={{
          float: "right",
          marginTop: "20px",
          width: "120px",
          marginLeft: "10px",
          background: "#A0D1F7",
          borderRadius: "10px 10px 10px 10px",
          boxShadow: "3px 3px 3px #A0D1F7",
          //border: "1px solid gray",
        }}
        onClick={() => {
          setC(true);
        }}
      >
        다음
      </Button>
      <Button
        style={{
          float: "right",
          marginTop: "20px",
          width: "120px",
          background: "#dcdcdc",
          borderRadius: "10px 10px 10px 10px",
          boxShadow: "3px 3px 3px #dcdcdc",
          //border: "1px solid gray",
        }}
        onClick={() => {
          setB(false);
          setComCommon({
            a: false,
            b: false,
            c: false,
            d: false,
            e: false,
            f: false,
          });
        }}
        disabled={c}
      >
        이전
      </Button>
      <br />
      <br />
      <br />
      <br />
    </>
  );
};

export default Common;
