import { Button } from "antd";
import React, { useState } from "react";
import ProjectCalPage from "../projectCalPage";
import Ku from "./ku.png";
const ProjectMain = () => {
  const [divDisabled, setDivDisabled] = useState(false);
  return (
    <>
      {!divDisabled ? (
        <div
          style={{
            height: "100vh",
          }}
        >
          <img
            src={Ku}
            style={{
              width: "150px",
              height: "100px",
              marginTop: "20px",
              marginLeft: "20px",
            }}
          />
          <div style={{}}>
            <span
              style={{
                marginLeft: "30px",
                fontSize: "60px",
                fontWeight: "bold",
                fontFamily: "굴림",
              }}
            >
              졸업요건진단기
            </span>
            <span style={{ marginRight: "20px", fontSize: "25px" }}>
              BETA{" "}
              <Button
                danger
                style={{ marginLeft: "55%" }}
                onClick={() => {
                  setDivDisabled(true);
                }}
              >
                졸업확인하기
              </Button>
            </span>
          </div>

          <div
            style={{
              marginLeft: "20px",
              marginRight: "20px",
              height: "70vh",
              border: "1px solid black",
            }}
          >
            <div
              style={{
                fontSize: "22px",
                marginLeft: "50px",
                marginRight: "50px",
                marginTop: "80px",
              }}
            >
              안녕하세요. 이 사이트는 '2021년 산업경영공학종합설계' 프로젝트의
              일환으로 제작되었습니다. <br />
              <br />
              해당 서비스는 경기대학교 산업경영공학과 4학년 학생들이 보다
              편리하게 자신의 졸업 가능 여부를 확인할 수 있도록 제작되었습니다.
              하지만 경기대학교에서 공식적으로 제공하는 '예비졸업사정'은
              아니기에, 보다 정확한 결과를 원하시는 분께서는 시기에 맞춰
              학과사무실에 연락하시길 바랍니다. 또한, 해당 졸업 가능 여부를
              확인하기 위해 입력되는 모든 값은 서버에 전혀 저장되지 않습니다.
              <br />
              <br />
              학과, 학번마다 졸업요건에 큰 차이가 존재해 아직은 '산업경영공학과
              (2017~2020학번)'에 한해서 서비스가 제공되지만, 추가적으로 타과에
              대해서도 요청이 들어오면 추가될 예정입니다.
              <br />
              <br />
              제작자 : 산업경영공학과 서지형(17), 윤수인(17), 임다혜(17),
              김연우(16)
            </div>
          </div>
        </div>
      ) : (
        <ProjectCalPage />
      )}
    </>
  );
};

export default ProjectMain;
