import {
  Tooltip,
  Checkbox,
  Tabs,
  Button,
  Radio,
  Input,
  Space,
  Card,
} from "antd";

import TabTable from "./TabTable";
import React, { useEffect, useState } from "react";
import { major, refinement, common } from "./data";
import { Select } from "antd";
import Track from "./track";
import Common from "./common";
import Department from "./department";

const { Option } = Select;

const { TabPane } = Tabs;

const Result = ({
  v1,
  v2,
  v3,
  totalVal,
  trackVal,
  tabTrackVal,
  comCommon,
  department,
  design,
  mustState,
  education,
}) => {
  const [t, setT] = useState(false);
  const [c, setC] = useState(0);
  const [d, setD] = useState(false);
  console.log("department >>> : ", department);
  useEffect(() => {
    if (trackVal === "시스템분석/경영공학") {
      tabTrackVal.a < 9 ? setT(false) : setT(true);
    }
    if (trackVal === "품질/정보") {
      tabTrackVal.b < 10 ? setT(false) : setT(true);
    }
    if (trackVal === "생산/물류/인간공학") {
      tabTrackVal.c < 8 ? setT(false) : setT(true);
    }
    let cc = 0;

    comCommon.a && (cc = cc + 1);
    comCommon.b && (cc = cc + 1);
    comCommon.c && (cc = cc + 1);
    comCommon.d && (cc = cc + 1);
    comCommon.e && (cc = cc + 1);
    comCommon.f && (cc = cc + 1);
    if (department.a === true || department.b === true) {
      setD(true);
    } else {
      setD(false);
    }
    setC(cc);
  }, [
    v1,
    v2,
    v3,
    trackVal,
    tabTrackVal,
    comCommon,
    department,
    design,
    mustState,
    education,
  ]);
  console.log("v2 + v3 >>> : ", v2 + v3);
  return (
    <>
      <Card
        style={{
          width: "1250px",
          border: "1px solid gray",
          marginTop: "10px",
          borderRadius: "20px 20px 20px 20px",
          textAlign: "center",
        }}
      >
        {[
          [
            `전공 이수학점 ${v1}학점/67학점으로 ${
              v1 < 136 ? "불만족하였습니다." : "만족하였습니다."
            }`,
            v1 < 136 ? "red" : "#41A541",
          ],
          [
            `교양 이수학점 ${v2 + v3}학점/51학점으로 ${
              v2 + v3 > 21 ? "만족하였습니다." : "불만족하였습니다."
            }`,
            v2 + v3 > 21 ? "#41A541" : "red",
          ],
          [
            `${
              mustState === 8
                ? "필수 전공을 모두 수강하였습니다."
                : "필수 전공 수강을 완료해 주십시요."
            }
            `,
            mustState < 8 ? "red" : "#41A541",
          ],
          [
            `${
              education.a > 1 &&
              education.b > 0 &&
              // education.c > 0 &&
              // education.d > 0 &&
              education.e > 0 &&
              education.f > 0 &&
              education.g > 1
                ? "필수 교양을 모두 수강하였습니다."
                : "필수 교양 수강을 완료해 주십시요."
            }
            `,
            education.a > 1 &&
            education.b > 0 &&
            // education.c > 0 &&
            // education.d > 0 &&
            education.e > 0 &&
            education.f > 0 &&
            education.g > 1
              ? "#41A541"
              : "red",
          ],
          [
            `설계 학점 ${design}학점/12학점으로 ${
              design < 12 ? "불만족하였습니다." : "만족하였습니다."
            }`,
            design < 12 ? "red" : "#41A541",
          ],
          [
            `트랙 이수가 ${t ? "완료 되었습니다." : "완료되지 않았습니다."}`,
            !t ? "red" : "#41A541",
          ],
          [
            `MSC 이수학점이 ${v2}학점/30학점으로 공학인증을 ${
              v2 < 30 ? "불만족하였습니다." : "만족하였습니다."
            }`,
            v2 < 30 ? "red" : "#41A541",
          ],
          [
            `공통 졸업요건을 ${
              c < 2 ? "불만족하였습니다." : "만족하였습니다."
            }`,
            c < 2 ? "red" : "#41A541",
          ],
          [
            `학과 졸업요건을 ${!d ? "불만족하였습니다." : "만족하였습니다."}`,
            !d ? "red" : "#41A541",
          ],
        ].map((a, index) => {
          return (
            <>
              <div
                style={{
                  width: "90%",
                  background: a[1],
                  height: "40px",
                  marginLeft: "5%",
                }}
              >
                <span style={{ color: "white", fontSize: "25px" }}>{a[0]}</span>
              </div>
              {index === 8 ? "" : <br />}
            </>
          );
        })}
      </Card>
    </>
  );
};

export default Result;
