import {
  Tooltip,
  Checkbox,
  Tabs,
  Button,
  Radio,
  Input,
  Space,
  Card,
} from "antd";

import TabTable from "./TabTable";
import React, { useState } from "react";
import { major, refinement, common } from "./data";
import { Select } from "antd";
import Track from "./track";
import Common from "./common";

const { Option } = Select;

const { TabPane } = Tabs;

const Department = ({ d, c, setC, setD, department, setDepartment }) => {
  const onChange1 = (e) => {
    if (e === "be") {
      setDepartment({ a: false, b: false });
    } else {
      setDepartment({ ...department, a: e.target.checked });
    }
  };
  const onChange2 = (e) => {
    setDepartment({ ...department, b: e.target.checked });
  };
  const handleChange = (value) => {
    console.log(`selected ${value}`);
  };
  return (
    <>
      <span
        style={{
          fontSize: "20px",
          fontWeight: "bold",
          borderRadius: "20px 20px 20px 20px",
        }}
      >
        학과 졸업요건 진단
      </span>
      <br />
      <Card
        style={{
          width: "1250px",
          border: "1px solid gray",
          marginTop: "10px",
          borderRadius: "20px 20px 20px 20px",
        }}
      >
        <div
          style={{
            pointerEvents: d && "none",
            opacity: d && "0.4",
          }}
        >
          <Checkbox
            id={1}
            onChange={(e) => onChange1(e)}
            style={{ fontWeight: "bold" }}
          >
            졸업시험 통과
          </Checkbox>
          <br />
          <Checkbox
            id={2}
            onChange={(e) => onChange2(e)}
            style={{ fontWeight: "bold" }}
          >
            보유 자격증
          </Checkbox>
          <Select
            defaultValue="품질경영기사"
            style={{ width: 240, marginLeft: "20px" }}
            onChange={handleChange}
          >
            <Option value="품질경영기사">품질경영기사</Option>
            <Option value="정보처리기사">정보처리기사</Option>
            <Option value="인간공학기사">인간공학기사</Option>
            <Option value="물류관리사">물류관리사</Option>
            <Option value="유통관리사 2급">유통관리사 2급</Option>
            <Option value="국가공인데이터분석전문가(ADSP)">
              국가공인데이터분석전문가(ADSP)
            </Option>
            <Option value="CPIM">CPIM</Option>
          </Select>
        </div>
      </Card>
      <Button
        style={{
          float: "right",
          marginTop: "20px",
          marginLeft: "10px",
          background: "#FFDC37",
          borderRadius: "10px 10px 10px 10px",
          boxShadow: "3px 3px 3px #FFDC37",
          //border: "1px solid gray",
        }}
        onClick={() => {
          setD(true);
        }}
      >
        졸업요건진단
      </Button>
      <Button
        style={{
          float: "right",
          marginTop: "20px",
          width: "120px",
          background: "#dcdcdc",
          borderRadius: "10px 10px 10px 10px",
          boxShadow: "3px 3px 3px #dcdcdc",
          //border: "1px solid gray",
        }}
        onClick={() => {
          setC(false);
          setD(false);
          onChange1("be");
        }}
      >
        이전
      </Button>
      <br />
      <br />
      <br />
      <br />
      <hr />
      <hr />
      <br />
      <br />
    </>
  );
};

export default Department;
